//header
.header {
    background-color: #fff;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__list {
        display: flex;
    }
    &__item+&__item {
        margin-left: 40px;
    }
    &__item {
        color: #4d4d4d;
        font-weight: 600;
        font-size: 15px;
        line-height: 22px;
        transition: all 0.3s;
        cursor: pointer;
    }
    &__item:hover {
        transition: all 0.3s;
        color: #5e63ff;
    }
    &__widget {
        display: flex;
        align-items: center;
    }
    &__search {
        background: url(../img/search.svg);
        background-position: center;
        width: 24px;
        height: 24px;
        align-items: center;
        margin-right: 40px;
    }
    &__reg {
        cursor: pointer;
        padding: 10px 30px;
        background-color: #5e63ff;
        font-family: Poppins;
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 22px;
        border-radius: 5px;
        color: #fff;
        transition: all 0.3s;
    }
    &__reg:hover {
        background-color: #4d4d4d;
        transition: all 0.3s;
    }
}